.gallery-product {
  position: relative;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  row-gap: 110px;
  justify-content: space-between;
  padding-top: 35px;
  padding-left: 30px;
  padding-right: 30px;
  padding-bottom: 43px;

  @media (max-width: 1080px) {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 4.17vw;
    padding-left: 0;
    padding-right: 0;
  }

  @media (max-width: 767px) {
    grid-template-columns: 1fr;
    justify-items: center;
  }
}

.gallery-product li {
  position: relative;

  outline: 2px solid blue;
}

.gallery-product li:nth-child(2) {
  margin-top: 20px;

  @media (max-width: 767px) {
    margin-top: 0;
  }
}

.gallery-product li:nth-child(3) {
  align-self: end;

  @media (max-width: 1080px) {
    grid-column: 1 / -1;
    justify-self: center;
  }
}

.gallery-product__img {
  position: relative;
  display: flex;
  flex-grow: 1;
  align-items: center;
}

.gallery-product p {
  position: absolute;
  max-width: 240px;
  font-family: inherit;
  font-size: 29px;
  line-height: 1.10345;
  font-weight: 300;
  font-style: italic;
  z-index: 10;
}

.gallery-product a {
  color: #2c3370;
}

.gallery-product__item--1 p {
  top: 100px;
  right: 120px;
}

.gallery-product__item--2 p {
  top: 300px;
  left: 50%;
  transform: translateX(-50%);
}

.gallery-product__item--3 p {
  top: -123px;
  left: 100px;
}

.gallery-product p::after {
  position: absolute;
  display: flex;
  content: "";
  left: 50%;
  transform: translateX(-50%);
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -1;
}

.gallery-product__item--1 p::after {
  width: 348px;
  height: 247px;
  top: -75px;
  background-image: url("../img/goods-img-back-1.png");
}

.gallery-product__item--2 p::after {
  width: 387px;
  height: 352px;
  top: -150px;
  background-image: url("../img/goods-img-back-2.png");
}

.gallery-product__item--3 p::after {
  width: 348px;
  height: 247px;
  top: -80px;
  left: -25%;
  background-image: url("../img/goods-img-back-1.png");
  transform: scaleX(-1);
}
