.gallery-goods {
  position: relative;
  width: 100%;
  background-color: #ffffff;
  padding-bottom: 35px;
}

.gallery-goods--card {
  padding-top: 100px;
  padding-bottom: 73px;
}

.gallery-goods--basket {
  margin-top: 80px;
}

.gallery-goods__title {
  position: relative;
  margin: 0 auto;
  z-index: 15;
}

.gallery-goods__list {
  margin-top: 50px;
}

.gallery-goods--basket .goods-list li:hover .goods-list__name {
  color: #2b2a29;
  text-decoration: none;
}

.gallery-goods--card h2 {
  font-family: inherit;
  font-size: 58px;
  line-height: 62 / 58 * 1;
  font-weight: 700;
  font-style: italic;
  text-align: center;
  color: #98b500;
  margin-bottom: 115px;

  @media (max-width: 1600px) {
    font-size: 50px;
  }

  @media (max-width: 1280px) {
    font-size: 42px;
  }

  @media (max-width: 1080px) {
    font-size: 50px;
  }

  @media (max-width: 480px) {
    font-size: 42px;
  }
}
