.content-info,
.content-info input {
  font-family: inherit;
  font-size: 25px;
  line-height: 34 / 25 * 1;
  font-weight: 300;
  color: #292929;
}

.content-info p:not(:last-child),
.content-info ul:not(:last-child) {
  margin-bottom: 38px;
}

.content-info ul {
  margin-left: 26px;
}

.content-info li {
  position: relative;
  padding-left: 35px;
  margin-bottom: 7px;
}

.content-info li::before {
  position: absolute;
  display: inline-block;
  content: "";
  width: 12px;
  height: 12px;
  top: 25%;
  left: 0;
  background-color: #98b500;
  border-radius: 50%;
}

.content-info input {
  font-size: 23px;
  color: #333333;
  height: 34px;
  border-bottom: 1px solid #292929;
}
