.name {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -45px;

  outline: 2px solid blue;
}

.name__list {
  display: inline-block;
  text-align: center;

  transform-origin: bottom center;
}


.name__list--1 span:nth-child(1) {
  transform: rotateY(2deg);
}

.name__list--1 span:nth-child(2) {
  transform: rotateY(4deg);
}

.name__list--1 span:nth-child(3) {
  transform: rotateY(6deg);
}

.name__list--1 span:nth-child(4) {
  transform: rotateY(8deg);
}





.innovation-evreinov {
    width: 665px;
    height: 665px;
    border-radius: 50%;
    background-color: #fff;
    margin-top: -5px;
    margin-left: 230px;
    z-index: 200;
    animation-duration: 1.2s;
    animation-delay: 2s;
}





