.title-main {
  max-width: 700px;
  font-family: inherit;
  font-size: 58px;
  line-height: 62 / 58 * 1;
  font-weight: 700;
  font-style: italic;
  color: #98b500;
  padding-left: 5.52vw;

  margin: 0 auto;

  outline: 2px solid blue;

  @media (max-width: 1600px) {
    font-size: 50px;
  }

  @media (max-width: 1280px) {
    font-size: 42px;
  }

  @media (max-width: 1180px) {
    max-width: 50vw;
  }

  @media (max-width: 1080px) {
    font-size: 50px;
  }

  @media (max-width: 992px) {
    max-width: 65vw;
    font-size: 42px;
  }

  @media (max-width: 767px) {
    max-width: 55vw;
  }

  @media (max-width: 680px) {
    max-width: 75%;
    font-size: 38px;
  }

  @media (max-width: 480px) {
    font-size: 34px;
  }

  @media (max-width: 380px) {
    font-size: 28px;
  }
}

.title-main::before {
  position: absolute;
  content: "";
  width: 65px;
  width: 3.3vw;
  height: 7px;
  top: 1.5vw;
  left: 0;
  background-color: #98b500;

  @media (max-width: 1600px) {
    height: 6px;
  }

  @media (max-width: 1280px) {
    height: 4px;
  }

  @media (max-width: 1080px) {
    height: 5px;
  }

  @media (max-width: 992px) {
    height: 4px;
  }

  @media (max-width: 767px) {

  }

  @media (max-width: 680px) {

  }

  @media (max-width: 480px) {

  }
}
