.slider-main {
  height: clamp(860px, 80vw, 938px);

  @media (max-width: 1600px) {
    font-size: 28px;
  }

  @media (max-width: 1280px) {
    font-size: 25px;
  }

  @media (max-width: 1180px) {
    font-size: 28px;

  }

  @media (max-width: 1180px) {
    font-size: 28px;
  }

  @media (max-width:680px) {

  }
}

.slider-main__item {
  position: relative;
  display: grid;
  width: 33.33%;

  outline: 2px solid green;

  @media (max-width: 1180px) {

  }
}

.slider-main__item::after {
  content: "";
  grid-column: 2/3;
  grid-row: 2/3;
  z-index: -1;
}

.slider-main__item--1 {
  grid-template-columns: 0.5fr clamp(347px, 30vw, 421px) 0.5fr;
  grid-template-rows: 1fr max-content 2fr;

  outline: 2px solid green;
}

.slider-main__item--1::after {
  padding-top: 58.6%;
}

.slider-main__item--2 {
  grid-template-columns: 0.5fr clamp(380px, 30vw, 577px) 0.5fr;
  grid-template-rows: 0.2fr max-content 3fr;

  outline: 2px solid green;
}

.slider-main__item--2::after {
  padding-top: 73.8%;
}

.slider-main__item--3 {
  grid-template-columns: 0.5fr clamp(360px, 24vw, 465px) 0.5fr;
  grid-template-rows: 1fr max-content 1fr;

  outline: 2px solid green;
}

.slider-main__item--3::after {
  padding-top: 108.17%;
}


.slider-main__text {

}

.slider-main__desc p {
  position: relative;
}

.slider-main__item--1 .slider-main__img {

}

.slider-main__item img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
  object-position: center;
}



.slider-main__img {

}












