.blog-author__wrap {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 3vw;

  @media (max-width: 1080px) {
    grid-template-columns: max-content;
    padding-left: 0;
    justify-content: center;
  }
}

.blog-author__block {
  max-width: 38vw;

  @media (max-width: 1080px) {
    max-width: 68.24vw;
    margin: 0 auto;
    margin-top: 35px;
  }

  @media (max-width: 680px) {
    display: none;
  }
}

.blog-author__persons {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.blog-author__img:nth-child(1) {
  position: relative;
  width: 153px;
  height: 153px;
  margin-top: 17px;
}

.blog-author__img:nth-child(2) {
  position: relative;
  width: 172px;
  height: 172px;
  margin-left: -20px;
}

.blog-author__persons p {
  font-family: inherit;
  font-size: 34px;
  font-weight: 300;
  font-style: italic;
  color: #2c3370;

  @media (max-width: 1600px) {
    font-size: 28px;
  }

  @media (max-width: 1280px) {
    font-size: 25px;
  }

  @media (max-width: 1080px) {
    font-size: 28px;
  }

  @media (max-width: 767px) {
    font-size: 25px;
  }

  @media (max-width: 680px) {
    font-size: 32px;
  }

  @media (max-width: 480px) {
    font-size: 28px;
  }
}

.blog-author__persons::before,
.blog-author__persons::after {
  position: relative;
  content: "";
  width: 10.8vw;
  height: 9.43vw;
  background-image: url("../img/branch.png");
  background-repeat: no-repeat;
  background-size: 100% auto;
  background-position: 50% 50%;
  z-index: 20;

  @media (max-width: 1080px) {
    width: 19.35vw;
    height: 16.76vw;
  }
}

.blog-author__persons::before {
  left: -10px;
}

.blog-author__persons::after {
  top: 0;
  right: -10px;
  transform: scaleX(-1);
}

.blog-author__desc {
  margin-top: 65px;
  columns: 2 auto;
  column-gap: 4.69vw;

  @media (max-width: 1600px) {

  }

  @media (max-width: 1080px) {
    margin-top: 9.26vw;
  }

  @media (max-width: 992px) {
    columns: 1;
  }
}

.blog-author__desc p:not(:last-child) {
  font-family: inherit;
  font-size: 34px;
  line-height: 42 / 34 * 1;
  font-weight: 300;
  font-style: italic;
  margin-bottom: 2.6vw;

  @media (max-width: 1600px) {
    font-size: 28px;
  }

  @media (max-width: 1280px) {
    font-size: 25px;
  }

  @media (max-width: 1080px) {
    font-size: 28px;
  }

  @media (max-width: 767px) {
    font-size: 25px;
  }

  @media (max-width: 480px) {
    font-size: 21px;
  }
}

.blog-author__desc a {
  color: #2c3370;
}

a.blog-author__leaf {
  display: flex;
  justify-content: center;
  font-size: 34px;
  font-weight: 100;
  color: #ffffff;
  text-decoration: underline;
  text-align: center;
  background-image: url("../img/heart.svg");
  background-size: cover;
  background-position: 0 0;
  width: 155px;
  height: 121px;
  padding-top: 28px;
  margin: 0 auto;

  @media (max-width: 1600px) {
    font-size: 28px;
  }

  @media (max-width: 1280px) {
    font-size: 25px;
  }

  @media (max-width: 1080px) {
    font-size: 28px;
  }

  @media (max-width: 992px) {
    padding-top: 35px;
    margin-top: 35px;
  }

  @media (max-width: 767px) {
    font-size: 25px;
    margin-top: 30px;
  }

  @media (max-width: 680px) {
    font-size: 28px;
  }

  @media (max-width: 480px) {
    font-size: 25px;
    width: 121px;
    height: 92px;
    padding-top: 25px;
  }
}

.blog-author .title-main {
  position: relative;
  padding-left: 5.52vw;

  @media (max-width: 1080px) {
    max-width: 60%;
  }

  @media (max-width: 767px) {

  }

  @media (max-width: 680px) {
    max-width: 50%;
    font-size: 38px;
  }

  @media (max-width: 580px) {

  }

  @media (max-width: 480px) {
    max-width: 40%;
    font-size: 32px;

  }

  @media (max-width: 380px) {
    font-size: 28px;
  }
}

.blog-author .title-main::before {
  position: absolute;
  left: 0;

  @media (max-width: 1600px) {

  }

  @media (max-width: 1280px) {

  }

  @media (max-width: 1080px) {

  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 680px) {

  }

  @media (max-width: 480px) {

  }
}
